.header {

	&__about {
		padding: 10px 0;
		display: inline-block;
		float: left;
		text-decoration: none;
		color: #000;
		@include r(720) {
			float: right;
			margin-bottom: 10px;
			padding-top: 10px; } }
	&__address {
		padding: 10px 0;
		display: inline-block;
		margin-left: 10px; }

	&__contacts {
		display: inline-block;
		float: right;
		padding: 19px 0;
		text-decoration: none;
		color: #000;
		border-bottom: 1px dashed #fff;
		margin-left: 10px;
		@include r(720) {
			clear: right; } }
	&__phone {
		display: block;
		text-decoration: none;
		color: #000;
		font-weight: bold; }
	&__call {
		display: block;
		text-decoration: none;
		color: #000;
		border-bottom: 1px dashed #000; }
	&__logo {
		display: inline-block;
		vertical-align: middle; }

	&__text {
		margin-left: 10px;
		display: inline-block;
		vertical-align: middle; } }




.nav {}



.hero {

	background-size: cover;
	background-position: 50% 100%;
	background-repeat: no-repeat;
	position: relative;
	&:before {
		@include r(720) {
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			background: rgba(#000,.5);
			content: ""; } }
	.container {
		position: relative;
		height: 100vh;
		min-height: 500px; }

	&__nav {
		position: absolute;
		bottom: 60px;
		left: 0;
		right: 0;
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		@include r(720) {
			position: static;
			display: block;
			padding-top: 200px; }
		a {
			text-transform: uppercase;
			color: #fff;
			text-decoration: none;
			font-weight: 600; }
		li {
			@include r(720) {
				margin-bottom: 10px;
				text-align: center;
				font-size: 25px; } } } }

