html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-arrow {
  width: 0.69em;
  height: 1em;
  fill: #FFF;
}

.icon-check {
  width: 1.2em;
  height: 1em;
  fill: #E8663C;
}

.icon-cog {
  width: 1em;
  height: 1em;
  fill: #E8663C;
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

.icon-user {
  width: 1.05em;
  height: 1em;
  fill: #E8663C;
}

/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
}

.slick-track:before, .slick-track:after {
  content: "";
  display: table;
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}

[dir="rtl"] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

@font-face {
  font-family: "bebas-font";
  src: url("../fonts/bebas.woff2") format("woff2"), url("../fonts/bebas.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

body {
  font-family: Helvetica,sans-serif;
}

.container {
  max-width: 940px;
  margin: 0 auto;
}

@media only screen and (max-width: 960px) {
  .container {
    margin: 0 10px;
  }
}

.button {
  font-size: 12px;
  text-transform: uppercase;
  height: 31px;
  padding: 0 13px;
  display: inline-block;
  color: #fff;
  text-decoration: none;
  background: #e8663c;
  line-height: 31px;
  font-family: bebas,sans-serif;
}

.h3 {
  display: flex;
  font-size: 18px;
  color: #e8603c;
  text-transform: uppercase;
  text-decoration: none;
  margin-bottom: 23px;
}

.h3:after {
  content: "";
  background: red;
  flex: 1;
  margin-left: 20px;
  background: url(../img/pattern.png) 0 0;
}

.header__about {
  padding: 10px 0;
  display: inline-block;
  float: left;
  text-decoration: none;
  color: #000;
}

@media only screen and (max-width: 720px) {
  .header__about {
    float: right;
    margin-bottom: 10px;
    padding-top: 10px;
  }
}

.header__address {
  padding: 10px 0;
  display: inline-block;
  margin-left: 10px;
}

.header__contacts {
  display: inline-block;
  float: right;
  padding: 19px 0;
  text-decoration: none;
  color: #000;
  border-bottom: 1px dashed #fff;
  margin-left: 10px;
}

@media only screen and (max-width: 720px) {
  .header__contacts {
    clear: right;
  }
}

.header__phone {
  display: block;
  text-decoration: none;
  color: #000;
  font-weight: bold;
}

.header__call {
  display: block;
  text-decoration: none;
  color: #000;
  border-bottom: 1px dashed #000;
}

.header__logo {
  display: inline-block;
  vertical-align: middle;
}

.header__text {
  margin-left: 10px;
  display: inline-block;
  vertical-align: middle;
}

.hero {
  background-size: cover;
  background-position: 50% 100%;
  background-repeat: no-repeat;
  position: relative;
}

@media only screen and (max-width: 720px) {
  .hero:before {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.5);
    content: "";
  }
}

.hero .container {
  position: relative;
  height: 100vh;
  min-height: 500px;
}

.hero__nav {
  position: absolute;
  bottom: 60px;
  left: 0;
  right: 0;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

@media only screen and (max-width: 720px) {
  .hero__nav {
    position: static;
    display: block;
    padding-top: 200px;
  }
}

.hero__nav a {
  text-transform: uppercase;
  color: #fff;
  text-decoration: none;
  font-weight: 600;
}

@media only screen and (max-width: 720px) {
  .hero__nav li {
    margin-bottom: 10px;
    text-align: center;
    font-size: 25px;
  }
}

.header__wrap {
  display: flex;
  text-align: left;
  padding: 22px 0;
  align-items: baseline;
}

@media only screen and (max-width: 620px) {
  .header__wrap {
    display: block;
    text-align: center;
  }
}

@media only screen and (max-width: 620px) {
  .header__logo {
    margin-bottom: 20px;
  }
}

.header__nav {
  margin-left: auto;
  position: relative;
  top: -6px;
}

@media only screen and (max-width: 620px) {
  .header__nav {
    top: 0;
    text-align: center;
    line-height: 2;
  }
}

.footer {
  overflow: hidden;
  font-size: 12px;
  line-height: 1.4;
}

.footer__text p {
  margin-bottom: 1em;
}

.footer h2 {
  margin-bottom: 23px;
  font-size: 20px;
  text-transform: uppercase;
  font-weight: normal;
  font-family: bebas,sans-serif;
  color: #fff;
}

@media only screen and (max-width: 600px) {
  .footer h2 {
    margin-bottom: 5px;
  }
}

.footer__wrap {
  position: relative;
  padding: 30px;
  background: #e8663c;
  color: #fff;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 17px;
}

@media only screen and (max-width: 600px) {
  .footer__wrap {
    display: block;
  }
}

.footer__wrap:before, .footer__wrap:after {
  position: absolute;
  height: 100%;
  background: #f2ae9c;
  content: "";
  width: 1000px;
  top: 0;
}

.footer__wrap:before {
  right: 100%;
  margin-right: 10px;
}

.footer__wrap:after {
  left: 100%;
  margin-left: 10px;
}

@media only screen and (max-width: 600px) {
  .footer__col {
    margin-bottom: 40px;
  }
}

.footer__whitelinks a {
  color: #fff;
  text-decoration: none;
}

.tweet {
  margin-bottom: 1.2em;
}

.tweet__content {
  margin-bottom: .3em;
}

.tweet a {
  color: #f4b29d;
  text-decoration: none;
}
